import React, { useContext, useEffect, useState } from 'react'

import { BigNumber } from "@ethersproject/bignumber"
import { Web3Provider } from "@ethersproject/providers"

import { Address, BALANCE_RECORDS_CNT, CHALLENGES_LIMIT, POLLING_INTERVAL_MS, REWARDS_CNT_LIMIT, REWARD_UNIT, SLEEP_TIMEOUT_MS, ZERO_ADDRESS } from "../components/Parameters"
import { getReasonLabel, getRewardUnits } from '../components/Rules'
import { ChallengesType, RegisteredBalancesType, VerifyClaimType } from "../components/Challenges"

import { AppContext, getContractSigner } from "./AppContext"
import AppPage from "./AppPage"

import { LuminaRecords } from '../hardhat/typechain/LuminaRecords'
import { LuminaToken } from '../hardhat/typechain/LuminaToken'
import { LuminaAdmin } from '../hardhat/typechain/LuminaAdmin'
import { LuminaTrustee } from '../hardhat/typechain/LuminaTrustee'
import { ProgressContractLocker } from '../hardhat/typechain/ProgressContractLocker'
import { LuminaMarketing } from '../hardhat/typechain/LuminaMarketing'
import { Contract, ContractTransaction, Signer } from 'ethers'

import { ZERO } from "../utils/bigNumber"
import { getErrMsg } from '../utils/error'

export type ThemeType = "light"|"dark"

export type ContractsType = {provider: Web3Provider, signer: Signer, token: LuminaToken, records: LuminaRecords, admin: LuminaAdmin, trustee: LuminaTrustee, locker: ProgressContractLocker, marketing: LuminaMarketing}|null
export type BalancesType = {
    balance: BigNumber,
    balanceMatic: BigNumber,
    totalSupply: BigNumber,
    claimedTokens: BigNumber,
    availableTokens: BigNumber,
    lockedBalance: BigNumber,
    lockedAvailableBalance: BigNumber,
    totalLockedBalance: BigNumber,
}
export type AppDataType = {
    challengesAllowance: number
    basicChallenges: ChallengesType
    premiumChallenges: ChallengesType
    registeredBalances: RegisteredBalancesType
    balances: BalancesType
    commision: number
}

export interface AppProps {
    theme: ThemeType
    demo: boolean
    mobile: boolean
    sleeping: boolean
    provider: Web3Provider|null
    signer: Signer|null
    address: Address
    ownerAddress: string
    paused: boolean
    marketingPaused: boolean
    contracts: ContractsType

    challengesAllowance: number
    basicChallenges: ChallengesType
    premiumChallenges: ChallengesType
    registeredBalances: RegisteredBalancesType
    balances: BalancesType
    commision: number

    err: string
    loading: boolean

    runTx: <T extends Contract> (
        contract: T,
        txf: (t: T) => Promise<ContractTransaction>,
        f?: (t: T) => void
    ) => Promise<boolean>
    setErr: (err: string) => void
    setProvider: (provider: Web3Provider|null) => void
    setAddress: (address: Address) => void
    setCommision: (commision: number) => void
    verifyClaim: (blockNumber: BigNumber, miner: string, recipient: string, solution: BigNumber) => Promise<VerifyClaimType>
    claimReward: (blockNumber: BigNumber, miner: string, recipient: string, solution: BigNumber) => Promise<boolean>
    refresh: () => void
}

interface AppContainerProps {
}

const AppContainer: React.FC<AppContainerProps> = () => {
    const initChallenges: ChallengesType = {totalCnt: 0, indexes: [], blockNumbers: [], rewardUnits: [], challengeHashes: [], nexts: [], claimsCnt: [], claimed: []}
    const initRegisteredBalances: RegisteredBalancesType = {balances: [], blockNumbers: []}
    const initBalances: BalancesType = { balance: ZERO, balanceMatic: ZERO, totalSupply: ZERO, claimedTokens: ZERO, availableTokens: ZERO, lockedBalance: ZERO, lockedAvailableBalance: ZERO, totalLockedBalance: ZERO }
    const initAppData: AppDataType = {
        challengesAllowance: 0,
        basicChallenges: initChallenges,
        premiumChallenges: initChallenges,
        registeredBalances: initRegisteredBalances,
        balances: initBalances,
        commision: 0,
    }

    const [refreshCnt, setRefreshCnt] = useState(0)
    const [txCnt, setTxCnt] = useState(0)

    const [theme] = useState<ThemeType>("dark")
    const [demo, setDemo] = useState(false)
    const [mobile] = useState(true)
    const [sleeping, setSleeping] = useState(false)
    const [provider, setProvider] = useState<Web3Provider|null>(null)
    const [signer, setSigner] = useState<Signer|null>(null)
    const [contracts, setContracts] = useState<ContractsType>(null)
    const [address, setAddress] = useState<Address>(ZERO_ADDRESS)

    const [appData, setAppData] = useState<AppDataType>(initAppData)

    const [err, setErr] = useState("")
    const [loading, setLoading] = useState(false)
    const [ownerAddress, setOwnerAddress] = useState("")
    const [paused, setPaused] = useState(false)
    const [marketingPaused, setMarketingPaused] = useState(false)

    const appContext = useContext(AppContext)
    const { contextNetwork, rpcToken, rpcRecords, rpcAdmin, rpcTrustee, rpcLocker, rpcMarketing, incContextUrlIndex } = appContext

    useEffect(() => {
        if(address !== ZERO_ADDRESS && provider === null) {
            return
        }
        const remove = (a: Array<any>, n: number) => {
            if(a.length <= n) {
                return a
            }

            const na: Array<any> = []
            a.forEach((e, i) => n !== i && na.push(e))
            return na
        }
        
        const claimsListener = (blockNumber: BigNumber, recipient: string, miner:string, solution: BigNumber, commisionPrc: number, claimNo: number) => {
            // console.log("Claim", {blockNumber, recipient, miner, solution, commisionPrc, claimNo})
            const updateChallenges = (ch: ChallengesType) => {
                const len = ch.blockNumbers.length
                for(let i = 0; i < len; i++) {
                    if(blockNumber.eq(ch.blockNumbers[i])) {
                        const { 0:claimsCnt } = getRewardUnits(ch.challengeHashes[i])
                        const rewardsCntLimit = REWARDS_CNT_LIMIT(demo)
                        let rewardsCnt = claimsCnt
                        if(rewardsCnt > rewardsCntLimit) {
                            rewardsCnt = rewardsCntLimit
                        }
                        if(claimNo + 1 === rewardsCnt) {
                            const nch = { 
                                totalCnt: ch.totalCnt-1,
                                indexes: remove(ch.indexes, i),
                                blockNumbers: remove(ch.blockNumbers, i),
                                rewardUnits: remove(ch.rewardUnits, i),
                                challengeHashes: remove(ch.challengeHashes, i),
                                nexts: remove(ch.nexts, i),
                                claimsCnt: remove(ch.claimsCnt, i),
                                claimed: remove(ch.claimed, i),
                            }

                            return nch
                        } else if(claimNo + 1 > ch.claimsCnt[i]) {
                            const nch = { ...ch, claimsCnt: [...ch.claimsCnt]}
                            nch.claimsCnt[i] = claimNo + 1
                            if(recipient === address) {
                                nch.claimed = [...ch.claimed]
                                nch.claimed[i] = true
                            }
                            return nch
                        }
                    }
                }
                return ch
            }
            const updateBalances = (ch: ChallengesType, balances: BalancesType) => {
                const i = ch.blockNumbers.findIndex(bn => blockNumber.eq(bn))
                if(i < 0) {
                    return balances
                }
                const { 1: rewardUnits } = getRewardUnits(ch.challengeHashes[i])
                const reward = REWARD_UNIT.mul(rewardUnits).mul(ch.rewardUnits[i])
                if(reward) {
                    if(!balances || !reward) {
                        return balances
                    }
                    return {...balances, claimedTokens: balances.claimedTokens.add(reward), availableTokens: balances.availableTokens.sub(reward)}
                }
                return balances
            }

            setAppData(data => ({ 
                ...data, 
                balances: updateBalances(data.premiumChallenges, updateBalances(data.basicChallenges, data.balances)),
                basicChallenges: updateChallenges(data.basicChallenges),
                premiumChallenges: updateChallenges(data.premiumChallenges),
            }))
        }

        // event Commision(address indexed wallet, uint8 commisionPrc);
        const commisionListener = (wallet: Address, commisionPrc: number) => {
            // console.log("Commision", {wallet, commisionPrc})
            setCommision(commisionPrc)
        }

        // event RegisteredBalance(address indexed wallet, uint64 blockNumber, uint256 balance);
        const registeredBalanceListener = (address: Address, blockNumber: BigNumber, balance: BigNumber) => {
            // console.log("RegisteredBalance", {address, blockNumber, balance})
            setAppData(data => {
                const { balances, blockNumbers } = data.registeredBalances
                const newBalances = [balance].concat(balances.slice(0, BALANCE_RECORDS_CNT - 1))
                const newBlockNumbers = [blockNumber].concat(blockNumbers.slice(0, BALANCE_RECORDS_CNT - 1))
                const registeredBalances: RegisteredBalancesType = { balances: newBalances, blockNumbers: newBlockNumbers }
                return { 
                    ...data, 
                    registeredBalances,
                }
            })
            
        }

        // event AddChallenge(uint64 indexed blockNumber, uint256 challenge, bool premium);
        const addChallengeListener = async (blockNumber: BigNumber, challenge: BigNumber, premium: boolean) => {
            // console.log("AddChallenge", {blockNumber, challenge, premium})
            // const updateChallenges = (ch: ChallengesType) => {
            //     if(ch.blockNumbers.some(bn => bn === blockNumber)) {
            //         return ch
            //     }
            //     const nch: ChallengesType = { 
            //         totalCnt: ch.totalCnt+1,
            //         indexes: [...ch.indexes, 0],
            //         blockNumbers: [...ch.blockNumbers, blockNumber],
            //         rewardUnits: [...ch.rewardUnits, 1000],
            //         challengeHashes: [...ch.challengeHashes, challenge.toHexString()],
            //         nexts: [...ch.nexts, 0],
            //         claimsCnt: [...ch.claimsCnt, 0],
            //         claimed: [...ch.claimed, false],
            //     }
            //     return nch
            // }
            if(premium) {
                const premiumChallenges = await readChallenges(contracts, true)
                setAppData(data => ({ ...data, premiumChallenges }))
            } else {
                const basicChallenges = await readChallenges(contracts, false)
                setAppData(data => ({ ...data, basicChallenges }))
            }
        }

        // event Transfer(address indexed from, address indexed to, uint256 value);
        const transferListener = (from: Address, to: Address, value: BigNumber) => {
            // console.log("transfer from:", from, "to:", to, value.toString())
            setAppData(data => ({ 
                ...data, 
                balances: { ...data.balances, balance: to === address ? data.balances.balance.add(value) : data.balances.balance.sub(value) },
            }))
        }

        const readChallenges = async (contracts: ContractsType, premium: boolean) => {
            const basicCha = contracts ? await contracts.admin.readChallenges(premium, CHALLENGES_LIMIT) : await rpcAdmin.readChallenges(premium, CHALLENGES_LIMIT)
            const {totalCnt, indexes, blockNumbers, rewardUnits, challengeHashes, nexts, claimsCnt, claimed} = basicCha
            const ch: string[] = challengeHashes.map(n => n.toHexString())
            return {totalCnt, indexes, blockNumbers, rewardUnits, challengeHashes: ch, nexts, claimsCnt, claimed}
        }
    
        const refresh = async () => {
            // console.log("RefreshCnt:", refreshCnt)
            setErr("")
            setLoading(true)
            try {
                if(provider && !(signer && contracts && contracts.provider === provider && contracts.signer === signer)) {
                    const signer = provider.getSigner()
                    const token     = getContractSigner(contextNetwork, "token", signer) as LuminaToken
                    const records   = getContractSigner(contextNetwork, "records", signer) as LuminaRecords
                    const admin     = getContractSigner(contextNetwork, "admin", signer) as LuminaAdmin
                    const trustee   = getContractSigner(contextNetwork, "trustee", signer) as LuminaTrustee
                    const locker    = getContractSigner(contextNetwork, "locker", signer) as ProgressContractLocker
                    const marketing = getContractSigner(contextNetwork, "marketing", signer) as LuminaMarketing
                    const ctrs = { provider, signer, token, records, admin, trustee, locker, marketing }
                    setSigner(signer)
                    setContracts(ctrs)
                    return
                }
                const [
                    premiumChallenges,
                    basicChallenges,
                    demo,
                    ownerAddress,
                    paused,
                    marketingPaused,
                    challengesAllowance,
                    totalSupply,
                    totalLockedBalance,
                    claimedTokens,
                ] = await Promise.all([
                    readChallenges(contracts, true),
                    readChallenges(contracts, false),
                    rpcTrustee.DEMO(),
                    rpcTrustee.owner(),
                    rpcTrustee.paused(),
                    rpcMarketing.paused(),
                    rpcAdmin.getChallengesAllowance(),
                    rpcToken.totalSupply(),
                    rpcLocker.totalLockedBalance(),
                    rpcTrustee.getClaimedTokens(),
                ])

                const initRegisteredBalances: RegisteredBalancesType = {balances: [], blockNumbers: []}

                // Balances
                const [
                    balance,
                    balanceMatic,
                    lockedBalance,
                    lockedAvailableBalance,
                    availableTokens,
                    commision,
                    registeredBalances,
                ] = provider && contracts ? await Promise.all([
                    rpcToken.balanceOf(address),
                    provider.getBalance(address),
                    rpcLocker.balanceOf(address),
                    rpcLocker.availableBalanceOf(address),
                    rpcToken.balanceOf(rpcTrustee.address),
                    rpcRecords.getCommision(address),
                    contracts.records.getBalances(),
                ]) : [
                    ZERO,
                    ZERO,
                    ZERO,
                    ZERO,
                    ZERO,
                    0,
                    initRegisteredBalances,
                ]

                const balances = { balance, balanceMatic, totalSupply, claimedTokens, availableTokens, lockedBalance, lockedAvailableBalance, totalLockedBalance }

                setDemo(demo)
                setOwnerAddress(ownerAddress)
                setPaused(paused)
                setMarketingPaused(marketingPaused)

                const data = {
                    challengesAllowance,
                    basicChallenges,
                    premiumChallenges,
                    registeredBalances,
                    balances,
                    commision,
                }
                setAppData(data)
            } catch(ex) {
                console.log(ex)
                incContextUrlIndex()
                // setErr(getErrMsg(ex))
            } finally {
                setLoading(false)
            }
        }

        refresh()

        let prevBalance = ZERO
        const timerStartMs = new Date().getTime()
        let subscribed = false
        let subscribeCancel = () => {}
        const timerListener = async () => {
            if(!provider) {
                return
            }
            try {
                setErr("")
                const timeMs = new Date().getTime()
                if((timeMs - timerStartMs) >= SLEEP_TIMEOUT_MS) {
                    subscribeCancel()
                    setSleeping(true)
                    return
                }
                const balance = await provider.getBalance(address)
                if (!prevBalance.eq(balance)) { 
                    setAppData(data => ({...data, balances: {...data.balances, balanceMatic: balance}}))
                }
                prevBalance = balance

            } catch(ex) {
                console.log(ex)
                incContextUrlIndex()
                // setErr(getErrMsg(ex))
            }
        }

        const subscribe = () => {
            // console.log("Subscribing...")
            const timerId = setInterval(timerListener, POLLING_INTERVAL_MS)
            rpcTrustee.on("Claim", claimsListener)
            rpcAdmin.on("AddChallenge", addChallengeListener)
            const transferFilter1 = rpcToken.filters.Transfer(address, null, null)
            rpcToken.on(transferFilter1, transferListener)
            const transferFilter2 = rpcToken.filters.Transfer(null, address, null)
            rpcToken.on(transferFilter2, transferListener)
            const commisionFilter = rpcRecords.filters.Commision(address, null)
            rpcRecords.on(commisionFilter, commisionListener)
            const registeredBalanceFilter = rpcRecords.filters.RegisteredBalance(address, null, null)
            rpcRecords.on(registeredBalanceFilter, registeredBalanceListener)
            subscribed = true
        
            return () => {
                // console.log("Cancelling...")
                if(!subscribed) {
                    return
                }
                clearTimeout(timerId)
                rpcTrustee.off("Claim", claimsListener)
                rpcAdmin.off("AddChallenge", addChallengeListener)
                rpcToken.off(transferFilter1, transferListener)
                rpcToken.off(transferFilter2, transferListener)
                rpcRecords.off(commisionFilter, commisionListener)
                rpcRecords.off(registeredBalanceFilter, registeredBalanceListener)
                subscribed = false
            }
        }

        subscribeCancel = subscribe()
        return subscribeCancel
    }, [refreshCnt, contextNetwork, provider, signer, contracts, rpcToken, rpcRecords, rpcAdmin, rpcTrustee, rpcLocker, rpcMarketing, address, demo, incContextUrlIndex])

    async function runTx<T extends Contract> (
        contract: T,
        txf: (t: T) => Promise<ContractTransaction>,
        f?: (t: T) => void
    ) {
        setErr("")
        try {
            const t = contract
            if (!t) {
                throw Error("Contract instance not ready")
            }
            const tx = await txf(t)
            const cr = await tx.wait()
            if(cr.confirmations <= 0) {
                throw new Error("No confirmations in contract receipt")
            }
            if(f) {
                f(t)
            }
            setTxCnt(txCnt + 1)
        } catch(ex) {
            const msg = getErrMsg(ex)
            window.alert(msg)
            return false
        }
        return true
    }

    const verifyClaim = async (blockNumber: BigNumber, miner: string, recipient: string, solution: BigNumber) => {
        if(paused) {
            return { rewardUnits: 0, reason: 101, premium: false, rewardsCnt: 0 }
        }
        try {
            const { rewardUnits, reason, premium, rewardsCnt } = await rpcTrustee.verifyClaim(blockNumber, miner, recipient, solution)

            return { rewardUnits, reason, premium, rewardsCnt }
        } catch(ex) {
            return { rewardUnits: 0, reason: 100, premium: false, rewardsCnt: 0 }
        }
    }

    const claimAlert = (blockNumber: BigNumber, reason: number, rewardUnits: number) => {
        if(reason === 0) {
        } else {
            window.alert("Claim on block #"+blockNumber.toString()+"\nStatus: FAILED\nReason: " + getReasonLabel(reason))
        }
    }

    const claimReward = async (blockNumber: BigNumber, miner: string, recipient: string, solution: BigNumber) => {
        if(!contracts) {
            window.alert("Please connect to your wallet to claim your reward")
            return false
        }
        const { rewardUnits, reason } = await verifyClaim(blockNumber, miner, recipient, solution)

        if(reason === 0) {
            await runTx(contracts.trustee, t => t.claimReward(blockNumber, miner, recipient, solution))
            return true
        }
        claimAlert(blockNumber, reason, rewardUnits)
        return false
    }

    const setCommision = (commision: number) => setAppData(data => ({...data, commision}))

    const refresh = () => {
        setSleeping(false)
        setRefreshCnt(refreshCnt => refreshCnt + 1)
    }

    const {
        challengesAllowance,
        basicChallenges,
        premiumChallenges,
        registeredBalances,
        balances,
        commision
    } = appData

    const props: AppProps = {
        theme,
        demo,
        mobile,
        sleeping,
        contracts,
        provider,
        signer,
        address,

        challengesAllowance,
        basicChallenges,
        premiumChallenges,
        registeredBalances,
        balances,
        commision,

        err,
        loading,
        ownerAddress,
        paused,
        marketingPaused,

        runTx,
        setErr,
        setProvider,
        setAddress,
        setCommision,
        verifyClaim,
        claimReward,
        refresh,
    }

    return <AppPage {...props}/>
}

export default AppContainer
