import React from 'react'

import { AppProps } from '../app/AppContainer'

const Management: React.FC<AppProps> = (props) => {

    const { address, contracts, ownerAddress, paused, runTx } = props

    const handlePause = async (e: React.MouseEvent<HTMLButtonElement, MouseEvent>) => {
        e.preventDefault()
        if(!contracts) {
            return
        }
        runTx(contracts.trustee, t => t.pause())
    }

    const handleUnpause = async (e: React.MouseEvent<HTMLButtonElement, MouseEvent>) => {
        e.preventDefault()
        if(!contracts) {
            return
        }
       runTx(contracts.trustee, t => t.unpause())
    }

    return <div>
        {ownerAddress === address ? <>
            <h2>Management</h2>
            <table style={{marginLeft: "auto", marginRight: "auto"}}>
                <thead>
                <tr>
                    <th>Paused: {paused ? "YES" : "NO"}</th>
                </tr>
                </thead>
                <tbody>
                <tr>
                    <td>
                        <button disabled={paused} onClick={(e) => handlePause(e)}>Pause</button>
                        <button disabled={!paused} onClick={(e) => handleUnpause(e)}>Unpause</button>
                    </td>
                </tr>
                </tbody>
            </table>
        </> : null }
    </div>
}

export default Management
