import LuminaRecordsPolygonDeployment from "../hardhat/deployments/polygon/LuminaRecords.json"
import LuminaRecordsMumbaiDeployment from "../hardhat/deployments/mumbai/LuminaRecords.json"
import LuminaRecordsLocalhostDeployment from "../hardhat/deployments/localhost/LuminaRecords.json"
import { LuminaRecords } from "../hardhat/typechain/LuminaRecords"
import { LuminaRecords__factory } from "../hardhat/typechain/factories/LuminaRecords__factory"

import LuminaAdminPolygonDeployment from "../hardhat/deployments/polygon/LuminaAdmin.json"
import LuminaAdminMumbaiDeployment from "../hardhat/deployments/mumbai/LuminaAdmin.json"
import LuminaAdminLocalhostDeployment from "../hardhat/deployments/localhost/LuminaAdmin.json"
import { LuminaAdmin } from "../hardhat/typechain/LuminaAdmin"
import { LuminaAdmin__factory } from "../hardhat/typechain/factories/LuminaAdmin__factory"

import LuminaTrusteePolygonDeployment from "../hardhat/deployments/polygon/LuminaTrustee.json"
import LuminaTrusteeMumbaiDeployment from "../hardhat/deployments/mumbai/LuminaTrustee.json"
import LuminaTrusteeLocalhostDeployment from "../hardhat/deployments/localhost/LuminaTrustee.json"
import { LuminaTrustee } from "../hardhat/typechain/LuminaTrustee"
import { LuminaTrustee__factory } from "../hardhat/typechain/factories/LuminaTrustee__factory"

import ProgressContractLockerPolygonDeployment from "../hardhat/deployments/polygon/ProgressContractLocker.json"
import ProgressContractLockerMumbaiDeployment from "../hardhat/deployments/mumbai/ProgressContractLocker.json"
import ProgressContractLockerLocalhostDeployment from "../hardhat/deployments/localhost/ProgressContractLocker.json"
import { ProgressContractLocker } from "../hardhat/typechain/ProgressContractLocker"
import { ProgressContractLocker__factory } from "../hardhat/typechain/factories/ProgressContractLocker__factory"

import LuminaMarketingPolygonDeployment from "../hardhat/deployments/polygon/LuminaMarketing.json"
import LuminaMarketingMumbaiDeployment from "../hardhat/deployments/mumbai/LuminaMarketing.json"
import LuminaMarketingLocalhostDeployment from "../hardhat/deployments/localhost/LuminaMarketing.json"
import { LuminaMarketing } from "../hardhat/typechain/LuminaMarketing"
import { LuminaMarketing__factory } from "../hardhat/typechain/factories/LuminaMarketing__factory"

import { DEFAULT_CHAIN_ID, POLLING_INTERVAL_MS } from '../components/Parameters'

import appConfig from "./AppConfig.json"

export type AppContextNetworkType = "polygon"|"mumbai"|"localhost"|"unknown"
export type AppContextContractType = "token"|"records"|"admin"|"trustee"|"locker"|"marketing"

export interface IAppContext {
    referrer: string
    contextNetwork: AppContextNetworkType
    chainId: number

    rpcProvider: JsonRpcProvider|WebSocketProvider
    rpcToken: LuminaToken
    rpcRecords: LuminaRecords
    rpcAdmin: LuminaAdmin
    rpcTrustee: LuminaTrustee
    rpcLocker: ProgressContractLocker
    rpcMarketing: LuminaMarketing

    setContextNetwork(network: AppContextNetworkType): void
    incContextUrlIndex(): void
}

export const deployments = {
    polygon: {
        token: LuminaTokenPolygonDeployment.address,
        records: LuminaRecordsPolygonDeployment.address,
        admin: LuminaAdminPolygonDeployment.address,
        trustee: LuminaTrusteePolygonDeployment.address,
        locker: ProgressContractLockerPolygonDeployment.address,
        marketing: LuminaMarketingPolygonDeployment.address,
    },
    mumbai: {
        token: LuminaTokenMumbaiDeployment.address,
        records: LuminaRecordsMumbaiDeployment.address,
        admin: LuminaAdminMumbaiDeployment.address,
        trustee: LuminaTrusteeMumbaiDeployment.address,
        locker: ProgressContractLockerMumbaiDeployment.address,
        marketing: LuminaMarketingMumbaiDeployment.address,
    },
    localhost: {
        token: LuminaTokenLocalhostDeployment.address,
        records: LuminaRecordsLocalhostDeployment.address,
        admin: LuminaAdminLocalhostDeployment.address,
        trustee: LuminaTrusteeLocalhostDeployment.address,
        locker: ProgressContractLockerLocalhostDeployment.address,
        marketing: LuminaMarketingLocalhostDeployment.address,
    },
    // Unknown will fallback to polygon
    unknown: {
        token: LuminaTokenPolygonDeployment.address,
        records: LuminaRecordsPolygonDeployment.address,
        admin: LuminaAdminPolygonDeployment.address,
        trustee: LuminaTrusteePolygonDeployment.address,
        locker: ProgressContractLockerPolygonDeployment.address,
        marketing: LuminaMarketingPolygonDeployment.address,
    },
}

export const factories: {[key: string]: (address: string, signer: Signer|Provider) => Contract} = {
   token: LuminaToken__factory.connect,
   records: LuminaRecords__factory.connect,
   admin: LuminaAdmin__factory.connect,
   trustee: LuminaTrustee__factory.connect,
   locker: ProgressContractLocker__factory.connect,
   marketing: LuminaMarketing__factory.connect,
}

export const getContractSigner = (network: AppContextNetworkType, contract: AppContextContractType, _signer: Signer): Contract => {
    const contractAddress = deployments[network][contract]
    const connect = factories[contract]
    const instance = connect(contractAddress, _signer)
    return instance
}

const getContract = (network: AppContextNetworkType, contract: AppContextContractType, _provider: providers.Provider): Contract => {
    const contractAddress = deployments[network][contract]
    const connect = factories[contract]
    const instance = connect(contractAddress, _provider)
    return instance
}

export const getAppContext = (contextNetwork: AppContextNetworkType, contextUrlIndex: number, setContextNetwork: (network: AppContextNetworkType) => void, incContextUrlIndex: () => void): IAppContext => {
    const queryParams = new URLSearchParams(window.location.search)
    const referrer = queryParams.get('ref') || ""

    const config = appConfig[contextNetwork]
    const { chainId, name, url } = config
    const options = ({name, chainId: chainId || DEFAULT_CHAIN_ID})
    let rpcProvider
    if(Array.isArray(url)) {
        if(url.length === 0) {
            throw new Error("Empty url array in json config")
        }
        const n = contextUrlIndex % url.length
        const urln = url[n]
        const ws = urln.startsWith("ws://") || urln.startsWith("wss://")
        rpcProvider = ws ? new WebSocketProvider(urln, options) : new StaticJsonRpcProvider(urln, options)
        if(!ws) {
            rpcProvider.pollingInterval = POLLING_INTERVAL_MS
        }
    } else {
        const ws = url.startsWith("ws://") || url.startsWith("wss://")
        rpcProvider = ws ? new WebSocketProvider(url, options) : new StaticJsonRpcProvider(url, options)
        if(!ws) {
            rpcProvider.pollingInterval = POLLING_INTERVAL_MS
        }
    }
    const rpcToken = getContract(contextNetwork, "token", rpcProvider) as LuminaToken
    const rpcRecords = getContract(contextNetwork, "records", rpcProvider) as LuminaRecords
    const rpcAdmin = getContract(contextNetwork, "admin", rpcProvider) as LuminaAdmin
    const rpcTrustee = getContract(contextNetwork, "trustee", rpcProvider) as LuminaTrustee
    const rpcLocker = getContract(contextNetwork, "locker", rpcProvider) as ProgressContractLocker
    const rpcMarketing = getContract(contextNetwork, "marketing", rpcProvider) as LuminaMarketing

    return {
        referrer,
        contextNetwork,
        chainId,
        rpcProvider,
        rpcToken,
        rpcRecords,
        rpcAdmin,
        rpcTrustee,
        rpcLocker,
        rpcMarketing,

        setContextNetwork,
        incContextUrlIndex,
    }
}

export const AppContext: React.Context<IAppContext> = React.createContext({} as IAppContext)
