import React from 'react'

import { AppProps } from "./AppContainer"
import Web3Modal from "./Web3Modal"

import TokenPage from "../token/TokenPage"
import Referral from "../components/Referral"
import Err from '../components/Err'

const BigLogo = () => <img style={{ "margin": "5px" }} width={360} alt="" src="lumina-logo.png"/>

const AppPage: React.FC<AppProps> = (props: AppProps) => {
    const { err, setProvider, setAddress } = props

    return <div>
        <BigLogo/>
        <Err err={err}/>
        <Web3Modal setProvider={setProvider} setAddress={setAddress}/>
        <p/>
        <Referral/>
        <TokenPage {...props}/>
    </div>
}

export default AppPage
