import React, { useContext } from 'react'

import { AppContext } from '../app/AppContext'

import appConfig from "../app/AppConfig.json"

export default () => {
    const appContext = useContext(AppContext)
    const { contextNetwork } = appContext

    const config = appConfig[contextNetwork]

    return contextNetwork !== "polygon" && contextNetwork !== "unknown" ? 
        <h2 style={{color: "red"}}><span style={{fontWeight: "bold"}}>Network:</span> {config.display} ({config.chainId})</h2>
    : null
}
