import React from 'react'

import Colors from "../utils/colors"

export interface ErrProps {
    err: string
}

const Err: React.FC<ErrProps> = ({err}) => {
    if(!err) {
        return null
    }
    return <h2 style={Colors.red}>{err.toString().split("\n").map((line:string, key: number)=><div key={key}>{line}</div>)}</h2>
}

export default Err
