import React from 'react'

import { FontAwesomeIcon } from '@fortawesome/react-fontawesome'
import { CopyToClipboard } from 'react-copy-to-clipboard'

import IconButton from "../components/IconButton"

import { verifyAddress } from "../utils/hex"

import css from "./ConnectedAddress.module.css"

export interface AddressProps {
    children: string
    chainId: number
    logo: string
    logoName: string
    changeFn: () => void
    disconnectFn: () => void
}

const ConnectedAddress: React.FC<AddressProps> = ({children: address, chainId, logo, logoName, changeFn, disconnectFn}) => {
    const url = chainId === 80001 ? "http://mumbai.polygonscan.com/address" : "http://polygonscan.com/address"

    if(!address) {
        return null
    }

    address = address.trim()
    if(!address) {
        return null
    }

    const valid = verifyAddress(address)
    const shortAddress = address.substring(0, 8)+"-"+address.substring(address.length-8)
    return <span className={css.connect+(!valid?" "+css.invalid:"")}>
        {logo && <><img style={{position: "relative", top: "2px"}} title={logoName} width="16px" src={logo} alt={logoName}/>&nbsp;</>}
        <button className={css.address} onClick={changeFn}>
            <code>{shortAddress}</code>
        </button>
        <IconButton cn={css.button}>
            <FontAwesomeIcon title="Disconnect" icon="lock" onClick={disconnectFn}/>
        </IconButton>
        {address && <CopyToClipboard text={address}>
            <span title={"Copy: "+address}>
                <IconButton cn={css.button}>
                    <FontAwesomeIcon icon={["far", "copy"]}/>
                </IconButton>
            </span>
        </CopyToClipboard>}
        <IconButton url={url+"/"+address} cn={css.button}>
            <FontAwesomeIcon title={url+"/"+address} icon="external-link-alt"/>
        </IconButton>
    </span>
}

export default ConnectedAddress
