import React, { useContext } from 'react'

import { AppContext } from '../app/AppContext'
import { AppProps } from '../app/AppContainer'

import { TOKEN_SYMBOL as symbol, TOKEN_DECIMALS as tokenDecimals } from "../components/Parameters"
import Address from '../components/Address'

import { formatBalance } from '../utils/bigNumber'

const Coins: React.FC<AppProps> = (props) => {
    const appContext = useContext(AppContext)
    const { chainId, rpcToken } = appContext

    const { address, balances } = props

    const { totalSupply, claimedTokens, availableTokens, totalLockedBalance } = balances

    return <div>
        <h2>LUMINA Coins</h2>
        <p title="The address of the Lumina ERC20 coin contract">
            <span style={{fontWeight: "bold"}}>Contract:</span>
            &nbsp;
            <Address chainId={chainId}>{rpcToken.address}</Address>
        </p>
        {!address ? null : <table style={{marginLeft: "auto", marginRight: "auto"}}>
            <tbody>
            <tr>
                <th style={{textAlign: "right"}}>Total Supply:</th>
                <td style={{textAlign: "right"}}>
                    {formatBalance(totalSupply, tokenDecimals)} {symbol}
                </td>
            </tr>
            <tr>
                <th style={{textAlign: "right"}}>Locked:</th>
                <td style={{textAlign: "right"}}>
                    {formatBalance(totalLockedBalance, tokenDecimals)} {symbol}
                </td>
            </tr>
            <tr>
                <th style={{textAlign: "right"}}>Mined:</th>
                <td style={{textAlign: "right"}}>
                    {formatBalance(claimedTokens, tokenDecimals)} {symbol}
                </td>
            </tr>
            <tr>
                <th style={{textAlign: "right"}}>Available:</th>
                <td style={{textAlign: "right"}}>
                    {formatBalance(availableTokens, tokenDecimals)} {symbol}
                </td>
            </tr>
            </tbody>
        </table>}
    </div>
}

export default Coins
