import React, { useContext, useState, useEffect } from 'react'

import { AppProps } from '../app/AppContainer'
import { AppContext } from "../app/AppContext"
import Address from '../components/Address'

import { BigNumber } from "@ethersproject/bignumber"
import { TOKEN_DECIMALS as tokenDecimals } from "../components/Parameters"
import { formatBalance } from '../utils/bigNumber'

export type CampaignType = {
    tokenBudget: BigNumber,
    ethBudget: BigNumber,
    campaignId: number,
    valid: boolean,
    active: boolean,
}

const Marketing: React.FC<AppProps> = (props) => {
    const [marketingCampaignAddrs, setMarketingCampaignAddrs] = useState<Array<string>>([])
    const [marketingCampaigns, setMarketingCampaigns] = useState<{[key: string]: CampaignType}>({})

    const appContext = useContext(AppContext)
    const { chainId, rpcMarketing } = appContext

    const { address, contracts, ownerAddress, marketingPaused, runTx } = props

    const handlePause = async (e: React.MouseEvent<HTMLButtonElement, MouseEvent>) => {
        e.preventDefault()
        if(!contracts) {
            return
        }
        runTx(contracts.marketing, t => t.pause())
    }

    const handleUnpause = async (e: React.MouseEvent<HTMLButtonElement, MouseEvent>) => {
        e.preventDefault()
        if(!contracts) {
            return
        }
       runTx(contracts.marketing, t => t.unpause())
    }

    const refresh = () => {

        const _refresh = async () => {
            if(ownerAddress !== address) {
                return
            }
            const addrs = await rpcMarketing.getCampaigns()
            setMarketingCampaignAddrs(addrs)
            const cgns: {[key: string]: CampaignType} = {}
            addrs.forEach(async addr => {
                const cgn: CampaignType = await rpcMarketing.getCampaign(addr)
                cgns[addr] = cgn
            })
            setMarketingCampaigns(cgns)
        }

        _refresh()
    }

    useEffect(refresh, [ownerAddress, address, rpcMarketing])

    return <div>
        {ownerAddress === address ? <>
            <h2>Marketing</h2>
            <table style={{marginLeft: "auto", marginRight: "auto"}}>
                <thead>
                <tr>
                    <th>Paused: {marketingPaused ? "YES" : "NO"}</th>
                </tr>
                </thead>
                <tbody>
                <tr>
                    <td>
                        <button disabled={marketingPaused} onClick={(e) => handlePause(e)}>Pause</button>
                        <button disabled={!marketingPaused} onClick={(e) => handleUnpause(e)}>Unpause</button>
                    </td>
                </tr>
                </tbody>
            </table>
            <h2>Campaigns</h2>
            <table style={{marginLeft: "auto", marginRight: "auto"}}>
                <thead>
                <tr>
                    <th>Campaign Addr</th>
                    <th>ID</th>
                    <th>Budget (LUMI)</th>
                    <th>Active</th>
                </tr>
                </thead>
                <tbody>
                {marketingCampaignAddrs.map((addr, i) => {
                    const cgn = marketingCampaigns[addr]
                    if(cgn && !cgn.valid) {
                        return null
                    }
                    return <tr key={i}>
                        <td>
                            <Address chainId={chainId}>{addr}</Address>
                        </td>
                        <td>{cgn && cgn.campaignId}</td>
                        <td>{cgn && formatBalance(cgn.tokenBudget, tokenDecimals)}</td>
                        <td>{cgn && cgn.active ? "Yes" : "No"}</td>
                    </tr>
                })}
                </tbody>
            </table>
         </> : null }
    </div>
}

export default Marketing
