import React from 'react'

import { FontAwesomeIcon } from '@fortawesome/react-fontawesome'

import { Challenges } from "../components/Challenges"
import LoadingIcon from '../components/LoadingIcon'

import { AppProps } from '../app/AppContainer'

import Launch from "./Launch"
import YourWallet from './YourWallet'
import Coins from './Coins'
import Difficulties from "./Difficulties"
import RegisteredBalances from './RegisteredBalances'
import Contracts from './Contracts'
import Management from './Management'
import Marketing from './Marketing'
import colors from '../utils/colors'

const SmallLogo: React.FC<{loading: boolean, sleeping: boolean, refresh: ()=>void}> = ({loading, sleeping, refresh}) => (sleeping
    ? <button style={colors.red} onClick={refresh}>Click to Refresh...</button>
    : loading
    ? <LoadingIcon/>
    : <img width={24} alt="" src="logo256.png"/>)

const Demo: React.FC<{demo: boolean}> = ({demo}) => demo ? <h2 style={{color: "red"}}>DEMO MODE</h2> : null

const TokenPage: React.FC<AppProps> = (props: AppProps) => {

    const { basicChallenges, premiumChallenges, address, registeredBalances, demo, mobile, sleeping, contracts, loading, ownerAddress, paused, challengesAllowance, runTx, verifyClaim, claimReward, refresh } = props

    const handleAddChallenges = async (e: React.MouseEvent<HTMLButtonElement, MouseEvent>) => {
        e.preventDefault()
        if(!contracts) {
            return
        }
        await runTx(contracts.admin, t => t.addChallenges(challengesAllowance))
    }

    return <div>
        <Launch/>
        <p/>
        <SmallLogo loading={loading} sleeping={sleeping} refresh={refresh}/>
        <Demo demo={demo}/>
        <h2>Regular Challenges ({basicChallenges.blockNumbers.length})
            {!paused && challengesAllowance > 0 && contracts 
            ? <>
                &nbsp;
                <button title="Add More Challenges" disabled={paused || challengesAllowance <= 0} onClick={(e) => handleAddChallenges(e)}>
                <FontAwesomeIcon icon="plus"/>{challengesAllowance}</button>
            </> : null}
        </h2>
        <table style={{marginLeft: "auto", marginRight: "auto"}}>
            <tbody>
            <tr>
                <td>
                    <Challenges
                        challenges={basicChallenges}
                        group="basic"
                        address={address}
                        registeredBalances={registeredBalances}
                        demo={demo}
                        mobile={mobile}
                        verifyClaim={verifyClaim}
                        claimReward={claimReward}
                    />
                </td>
            </tr>
            </tbody>
        </table>
        <p/>
        <SmallLogo loading={loading} sleeping={sleeping} refresh={refresh}/>
        <Demo demo={demo}/>
        <h2>Premium Challenges ({premiumChallenges.blockNumbers.length})
            {!paused && challengesAllowance > 0 && contracts 
            ? <>
                &nbsp;
                <button title="Add More Challenges" disabled={paused || challengesAllowance <= 0} onClick={(e) => handleAddChallenges(e)}>
                <FontAwesomeIcon icon="plus"/>{challengesAllowance}</button>
            </> : null}
        </h2>
        <table style={{marginLeft: "auto", marginRight: "auto"}}>
            <tbody>
            <tr>
                <td>
                    <Challenges
                        challenges={premiumChallenges}
                        group="premium"
                        address={address}
                        registeredBalances={registeredBalances}
                        demo={demo}
                        mobile={mobile}
                        verifyClaim={verifyClaim}
                        claimReward={claimReward}
                    />
                </td>
            </tr>
            </tbody>
        </table>
        <p/>
        <SmallLogo loading={loading} sleeping={sleeping} refresh={refresh}/>
        <Difficulties/>
        <p/>
        <SmallLogo loading={loading} sleeping={sleeping} refresh={refresh}/>
        <YourWallet {...props}/>
        <p/>
        <SmallLogo loading={loading} sleeping={sleeping} refresh={refresh}/>
        <Coins {...props}/>
        {registeredBalances.blockNumbers.length > 0 ? <>
            <p/>
            <SmallLogo loading={loading} sleeping={sleeping} refresh={refresh}/>
        </> : null}
        <RegisteredBalances {...props}/>
        <p/>
        <SmallLogo loading={loading} sleeping={sleeping} refresh={refresh}/>
        <Contracts {...props}/>
        {ownerAddress === address ? <>
            <p/>
            <SmallLogo loading={loading} sleeping={sleeping} refresh={refresh}/>
        </> : null}
        <Management {...props}/>
        {ownerAddress === address ? <>
            <p/>
            <SmallLogo loading={loading} sleeping={sleeping} refresh={refresh}/>
        </> : null}
        <Marketing {...props}/>
        <p/>
        <SmallLogo loading={loading} sleeping={sleeping} refresh={refresh}/>
        <p/>
   </div>
}

export default TokenPage
