import React, {useContext, useEffect, useState} from 'react'
import { ExternalProvider, Web3Provider } from "@ethersproject/providers"
import web3modal from "web3modal"
import WalletConnectProvider from "@walletconnect/web3-provider"

import Authereum from "authereum"
import Fortmatic from "fortmatic"
import Portis from "@portis/web3"
import WalletLink from "walletlink"

import { AppContext } from "./AppContext"
import { AppContextNetworkType } from "./AppContext"
import ConnectedAddress from './ConnectedAddress'

import { ZERO_ADDRESS } from "../components/Parameters"
import Err from '../components/Err'
import Network from '../components/Network'
import { getErrMsg } from '../utils/error'

import appConfig from "./AppConfig.json"
import css from "./ConnectedAddress.module.css"

const appConfigChainMap: {[key: string]: AppContextNetworkType} = {}
Object.keys(appConfig).forEach((network: string) => {
  const config = appConfig[network as AppContextNetworkType]
  appConfigChainMap[config.chainId.toString()] = network as AppContextNetworkType
})

export interface Web3ModalProps {
  setProvider: (provider: Web3Provider|null) => void
  setAddress: (address: string) => void
}

type ConnectionType = { web3Modal: web3modal|null, connected: boolean, connection: ExternalProvider|null, provider: Web3Provider|null, chainId: number, address: string, cancel: (()=>void)|null, err: string }
const initConnection: ConnectionType = { web3Modal: null, connected: false, connection: null, provider: null, chainId: 0, address: ZERO_ADDRESS, cancel: null, err: "" }

const Web3Modal: React.FC<Web3ModalProps> = ({setProvider, setAddress}) => {
  const [refreshCnt, setRefreshCnt] = useState(0)
  const [conn, setConn] = useState(initConnection)

  const appContext = useContext(AppContext)
  const { contextNetwork, chainId: contextChainId, setContextNetwork } = appContext

  const config = appConfig[contextNetwork]

  const close = () => {
    // console.log("Closing...", conn)
  }

  const disconnect = async (conn: ConnectionType, close: boolean) => {
    if(conn.connected) {
      const { web3Modal, connection, cancel } = conn
      if(cancel) {
        cancel()
      }
      if(close && connection && (connection as any).close) {
        await (connection as any).close()
      }
      if(web3Modal) {
        web3Modal.clearCachedProvider()
      }
      setProvider(null)
      setConn(initConnection)
      if(!close) {
        setRefreshCnt(refreshCnt+1)
      }
    }
  }

  useEffect(() => {
    const chainChanged = (chainId: number) => {
      const network = appConfigChainMap[chainId.toString()]
      if(!network) {
        const err = "Network Not Supported (ChainId: "+chainId+")\nPlease Connect to Polygon Network"
        setProvider(null)
        setContextNetwork("unknown")
        setConn((conn: ConnectionType) => {
          return { ...conn, err }
        })
      } else if(contextChainId !== chainId) {
        setProvider(null)
        setContextNetwork(network)
        setConn((conn: ConnectionType) => {
          return { ...conn, err: "" }
        })
      }
    }
  
    const subscribeProvider = (provider: any) => {
      if (!provider.on) {
        return null
      }
  
      const _disconnect = () => close()
      const _accountsChanged = async (accounts: Array<string>) => {
        if(accounts.length > 0) {
          setAddress(accounts[0])
          setConn((conn: ConnectionType) => {
            return { ...conn, address: accounts[0] }
          })
        }
      }
      const _chainChanged = async (chainIdLike: number|string) => {
        const chainId = typeof(chainIdLike) === "number" ? chainIdLike : parseInt(chainIdLike, chainIdLike.startsWith("0x")?16:10)
        chainChanged(chainId)
      }
  
      provider.on("disconnect", _disconnect)
      provider.on("accountsChanged", _accountsChanged)
      provider.on("chainChanged", _chainChanged)
  
      if(provider.removeListener) {
        return () => {
          provider.removeListener("disconnect", _disconnect)
          provider.removeListener("accountsChanged", _accountsChanged)
          provider.removeListener("chainChanged", _chainChanged)
        }
      }
  
      return () => {}
    }
  
    async function connect() {
      const providerOptions = {
        walletconnect: {
          package: WalletConnectProvider, // required
          options: {
            infuraId: "INFURA_ID" // required
          }
        },
        portis: {
          package: Portis,
          options: {
            id: "PORTIS_ID" // required
          }
        },
        fortmatic: {
          package: Fortmatic,
          options: {
            key: "FORTMATIC_KEY", // required,
            network: { // if we don't pass it, it will default to localhost:8454
              rpcUrl: config.url,
              chainId: config.chainId
            }
          }
        },
        authereum: {
          package: Authereum
        },
        'custom-coinbase': {
          display: {
            logo: 'images/coinbase_wallet_appicon.svg', 
            name: 'CoinbaseWallet',
            description: 'Scan with WalletLink to connect',
          },
          options: {
            appName: 'LuminaCash', // Your app name
            networkUrl: config.url,
            chainId: config.chainId,
          },
          package: WalletLink,
          connector: async (_: any, options: any) => {
            const { appName, networkUrl, chainId } = options
            const walletLink = new WalletLink({
              appName
            })
            const provider = walletLink.makeWeb3Provider(networkUrl, chainId)
            await provider.enable()
            return provider
          },
        },
      }
  
      try {
        let web3Modal: web3modal
        let connection: ExternalProvider
        try {
            web3Modal = new web3modal({
            cacheProvider: true, // optional
            disableInjectedProvider: false,
            providerOptions // required
          })
  
          connection = await web3Modal.connect()
        } catch(ex) {
            if(ex === "Modal closed by user") {
              throw ex
            }
            web3Modal = new web3modal({
            cacheProvider: false, // optional
            disableInjectedProvider: true,
            providerOptions // required
          })
          web3Modal.clearCachedProvider()
  
          connection = await web3Modal.connect()
        }
        const provider = new Web3Provider(connection)
        const accounts = await provider.listAccounts()
        const address = accounts[0]
        const chainId = (await provider.getNetwork()).chainId
  
        setConn((conn: ConnectionType) => {
          if(conn.cancel) {
            conn.cancel()
          }
          const cancel = subscribeProvider(connection)
          return {
            web3Modal,
            connected: true,
            connection,
            provider,
            chainId,
            address,
            cancel,
            err: "",
          }
        })

        setAddress(address)
        if(chainId === config.chainId) {
          setProvider(provider)
        } else {
          chainChanged(chainId)
        }

      } catch(ex) {
        const msg = getErrMsg(ex)
        if(msg !== "Modal closed by user") {
          console.log("Web3Modal Error:", msg)
          setConn((conn: ConnectionType) => {
            return { ...conn, err: msg }
          })
        }
      }
    }
    
    const refresh = async () => {
      if (refreshCnt > 0 || localStorage.getItem("WEB3_CONNECT_CACHED_PROVIDER") !== null) {
        connect()
      }
    }
    refresh()
  }, [refreshCnt, config, contextChainId, setContextNetwork, setProvider, setAddress])

  const { web3Modal, connection, connected, chainId, address, err} = conn
  let userOptions = null
  let logo = ""
  let logoName = ""
  if(web3Modal && connection) {
    const a = (web3Modal as any).userOptions.filter((o: any) => (connection as any)["is"+o.name])
    if(a.length > 0) {
      userOptions = a[0]
      logo = userOptions.logo
      logoName = userOptions.name
    }
  }
  return <div>
    <Err err={err}/>
    <span title={connected ? "The address of your wallet" : "Connect to your wallet"}>
      {connected ? 
      <ConnectedAddress chainId={chainId} logoName={logoName} logo={logo} changeFn={() => disconnect(conn, false)} disconnectFn={() => disconnect(conn, true)}>{address}</ConnectedAddress> : 
      <button className={css.connect} onClick={() => setRefreshCnt(refreshCnt+1)}>Connect Wallet</button>}
    </span>
    <Network/>
    </div>
}

export default Web3Modal
