import React, { useContext } from 'react'

import { AppContext } from "../app/AppContext"

import Colors from "../utils/colors"

export default () => {
    const appContext = useContext(AppContext)
    const { referrer } = appContext

    if(!referrer) {
        return null
    }

    const address = referrer
    const shortAddress = address.substring(0, 8)+"-"+address.substring(address.length-8)

    return <div style={{...Colors.green, margin: "3px"}}>
        <div>&nbsp;</div>
        <h2>You have been referred by {shortAddress}.</h2>
        <h2>If you accept this referral, you will receive</h2>
        <h2>extra 0.1 LUMI with your first token mined.</h2>
        <h1><button style={{fontSize: "16px", fontWeight: "bold"}}>Accept Referral</button></h1>
        <div>&nbsp;</div>
    </div>
}
