import React, { useContext } from 'react'

import { FontAwesomeIcon } from '@fortawesome/react-fontawesome'
import { BigNumber } from 'ethers'

import { AppContext } from '../app/AppContext'
import { AppProps } from '../app/AppContainer'

import { TOKEN_SYMBOL as symbol, TOKEN_DECIMALS as tokenDecimals } from "../components/Parameters"
import Address from '../components/Address'
import IconButton from '../components/IconButton'
import CommisionSetting from '../components/CommisionSetting'

import { formatBalance, ZERO } from '../utils/bigNumber'

const YourWallet: React.FC<AppProps> = (props) => {
    const appContext = useContext(AppContext)
    const { chainId, rpcToken } = appContext

    const { address, registeredBalances, provider, contracts, balances, commision, paused, runTx, setCommision } = props

    const { balance, balanceMatic, lockedBalance, lockedAvailableBalance } = balances

    const faucetUrl = chainId === 31337 ? "https://matic.supply/" : chainId === 80001 ? "https://faucet.polygon.technology/": "https://matic.supply/"
    const tradeUrl = "https://quickswap.exchange/#/swap?outputCurrency="+rpcToken.address

    const handleLockBalance = async (e: React.MouseEvent<HTMLButtonElement, MouseEvent>) => {
        e.preventDefault()
        if(!contracts) {
            return
        }
        const { token, locker } = contracts
        const lockerAddress: any = locker && locker.address
        await runTx(token, t => t.approve(lockerAddress, balance))
        await runTx(locker, l => l.deposit(balance))
    }

    const handleWithdrawBalance = async (e: React.MouseEvent<HTMLButtonElement, MouseEvent>) => {
        e.preventDefault()
        if(!contracts) {
            return
        }
        const { locker } = contracts
        await runTx(locker, l => l.withdraw(lockedAvailableBalance))
    }

    const handleRegisterBalance = async (e: React.MouseEvent<HTMLButtonElement, MouseEvent>) => {
        e.preventDefault()
        if(!contracts) {
            return
        }
        const { token } = contracts
        await runTx(token, t => t.registerBalance())
    }

    async function handleSetCommision(commision: number) {
        if(!contracts) {
            return
        }
        const { records } = contracts
        setCommision(commision)
        const success = await runTx(records, t => t.setCommision(commision))
        if(success) {
            setCommision(commision)
        } else {
            window.alert("Could not change commision")
        }
    }

    return <div>
        <h2>YOUR Wallet</h2>
        <p title="The adddress of your wallet, currently connected to Metamask">
            {address ? <span style={{fontWeight: "bold"}}>
                Wallet:
                &nbsp;
                <Address chainId={chainId}>{address}</Address>
            </span> : null}
        </p>
        {provider ? <table style={{marginLeft: "auto", marginRight: "auto"}}>
            <tbody>
            <tr>
                <th style={{textAlign: "right"}}>LUMI:</th>
                <td style={{textAlign: "left", fontWeight: "bold"}}>
                    {formatBalance(balance, tokenDecimals)} {symbol}
                    &nbsp;{<IconButton url={tradeUrl}><FontAwesomeIcon icon="shopping-cart"/></IconButton>}
                </td>
                <td>
                    {balance.gte(BigNumber.from(10).pow(tokenDecimals+5)) ? <button onClick={(e) => handleLockBalance(e)}>Lock Balance</button> : null}
                    &nbsp;{balance.gt(ZERO)?<button title="Register Balance" disabled={paused} onClick={(e) => handleRegisterBalance(e)}><FontAwesomeIcon icon="share-square"/></button>:null}
                </td>
            </tr>
            <tr>
                <th style={{textAlign: "right"}}>MATIC:</th>
                <td style={{textAlign: "left"}}>
                    {formatBalance(balanceMatic, tokenDecimals)}
                    &nbsp;MATIC
                    {balanceMatic.eq(0) ? <span title={"You need MATIC to claim LUMI rewards...\nClick the link icon to open MATIC faucet at "+faucetUrl}>
                        &nbsp;
                        <FontAwesomeIcon color="red" icon="exclamation-triangle"/>
                        <IconButton url={faucetUrl}><FontAwesomeIcon  color="red" icon="external-link-alt"/></IconButton>
                    </span> : null}
                </td>
                <td>
                </td>
            </tr>
            {registeredBalances.blockNumbers.length > 0 ? <tr>
                <th style={{textAlign: "right"}}>Registered:</th>
                <td style={{textAlign: "left"}}>{formatBalance(registeredBalances.balances[0], tokenDecimals)} {symbol}</td>
                <td/>
            </tr> : null}
            {lockedBalance.gt(0) ? <tr>
                <th style={{textAlign: "right"}}>Locked:</th>
                <td style={{textAlign: "left"}}>{formatBalance(lockedBalance, tokenDecimals)} {symbol}</td>
            </tr> : null}
            {lockedAvailableBalance.gt(0) ? <tr>
                <th style={{textAlign: "right"}}>Withdrawable:</th>
                <td style={{textAlign: "left"}}>
                    {formatBalance(lockedAvailableBalance, tokenDecimals)} {symbol}
                    &nbsp;<button disabled={paused} onClick={(e) => handleWithdrawBalance(e)}>Withdraw</button>
                </td>
            </tr> : null}
            {address ? <tr title={"Portion of reward you are willing to share with any miner,\nwho would claim mine a reward for the benefit of your wallet.\n\nAllowed values: 0% or within range [10%-90%].\n\nThe default value is 22% but you can change it at any time."}>
                <th style={{textAlign: "right"}}>Commision:</th>
                <td style={{textAlign: "left"}}><CommisionSetting commision={commision} setCommision={handleSetCommision}/></td>
                <td style={{textAlign: "left"}}><FontAwesomeIcon icon="info"/></td>
            </tr> : null}
            </tbody>
        </table> : null}
    </div>
}

export default YourWallet
