
const blackList: {[key: string]: boolean} = {
    "header not found": true,
    "Internal error": true,
    "Internal JSON-RPC error.": true,
    "project ID request rate exceeded": true,
}

export const getErrMsg = (ex: any): string => {
    let msg = ex.data && ex.data.message ? ex.data.message : ex.message ? ex.message : ex.toString()
    if(msg.startsWith("Error: ")) {
        msg = msg.substring(7)
    }
    if(ex.data && ex.data.see) {
        msg += "\nSee: "+ex.data.see
    }
    const err0 = "VM Exception while processing transaction: revert "
    if(msg && msg.startsWith(err0)) {
        msg = msg.substring(err0.length)
    }
    if(blackList[msg]) {
        msg = ""
    }
    return msg
}
