import React, {useState} from 'react'

import { AppContext, AppContextNetworkType, getAppContext } from "./app/AppContext"
import AppContainer from './app/AppContainer'

import './components/Icons'

import './App.css'

function App() {
  const [contextNetwork, setContextNetwork] = useState<AppContextNetworkType>("polygon")
  const [contextUrlIndex, setContextUrlIndex] = useState<number>(0)

  return (
    <div className="App">
      <header className="App-header">
        <AppContext.Provider value={getAppContext(contextNetwork, contextUrlIndex, (contextNetwork: AppContextNetworkType) => setContextNetwork(contextNetwork), () => setContextUrlIndex(contextUrlIndex+1))}>
          <AppContainer/>
        </AppContext.Provider>
      </header>
    </div>
  );
}

export default App;
