import React from 'react'

import { BigNumber } from 'ethers'

import { AppProps } from '../app/AppContainer'

import { TOKEN_SYMBOL as symbol, TOKEN_DECIMALS as tokenDecimals } from "../components/Parameters"

import { formatBalance } from '../utils/bigNumber'

const RegisteredBalances: React.FC<AppProps> = (props) => {
    const { registeredBalances, balances } = props

    const { balance } = balances

    return <div>
        {registeredBalances.blockNumbers.length > 0 ? <>
            <h2>Registered Balances</h2>
            <p/>
            <table style={{marginLeft: "auto", marginRight: "auto"}}>
                <thead>
                    <tr>
                        <th style={{textAlign: "right"}}>Balance</th>
                        <th>Since Block</th>
                    </tr>
                </thead>
                <tbody>
                    {registeredBalances.balances
                    .slice(0, registeredBalances.blockNumbers.length)
                    .map((balance, i) => {
                        const blockNumber = registeredBalances.blockNumbers[i]
                        return <tr key={i}>
                            <td>{formatBalance(balance, tokenDecimals)} {symbol}</td>
                            <td>{blockNumber.toString()}</td>
                        </tr>
                    })}
                </tbody>
            </table>
            </> : balance.gte(BigNumber.from(10).pow(tokenDecimals-3)) 
            ? <p>Please register your current balance to be eligible for additional benefits</p>
        : null}
    </div>
}

export default RegisteredBalances
